import Layout from '../components/layout';
import {withRouter} from 'next/router';
import axios from 'axios';


class Post extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            post: [],
        };
    }

    componentDidMount(){
        //remove transparent header
        document.body.classList.remove('header-transparent');
        // get post data
        let url = 'https://blog.matrixki.com/wp-json/wp/v2/posts/'+this.props.router.query.id;
        axios.get(url)
        .then( res => {            
            return res.data;
        } )
        .then( post => {
            console.log(post);
            this.setState((state, props) => ({
                post: post,
            }));
        } );
    }

    render(){
        return (
            <Layout>
                <p>Post page</p>
            </Layout>
        );
    }
};
export default withRouter(Post);



